@import 'src/style/variable.scss';

.sortblock {
  width: 32px;
  height: 100%;
  margin-right: 15px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  display: none;
  &__body {
    width: 100%;
    height: 22px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  svg {
    width: 100%;
    height: 100%;
    fill: var(--searchBlock);
  }
  &__arrow {
    width: 12px;
    height: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    align-self: flex-end;
    transform: rotate(90deg);
  }

  &__block {
    width: 22px;
    height: 22px;
    cursor: pointer;
  }
  &.sort .sortblock__block {
    transform: rotate(180deg) scaleX(-1);
  }

  @media (max-width: 670px) {
    & {
      display: none;
    }
  }
}

.sortprice {
  display: flex !important;
  height: 100% !important;
  flex-direction: column;
  width: 80%;

  &__body {
    position: relative;
    width: 100%;
    height: 30px;
    left: 0;
    display: flex;
    align-items: center;
    margin: 15px 0 10px 0;

    input {
      width: 220px;
      position: absolute;
      appearance: none;
      background-color: transparent;
      pointer-events: none;
      height: 5px;
      margin: auto;
      top: 0;
      bottom: 0;
    }

    input::-webkit-slider-runnable-track,
    input::-moz-runnable-track,
    input::-ms-track {
      height: 5px;
      appearance: none;
    }

    input::-ms-track {
      height: 5px;
      appearance: none;
    }

    input::-webkit-slider-thumb {
      appearance: none;
      height: 1.5em;
      width: 1.5em;
      background: var(--oldyellow);
      cursor: pointer;
      border-radius: 100%;
      pointer-events: auto;
    }

    input::-moz-range-thumb {
      appearance: none;
      height: 1.5em;
      width: 1.5em;
      background: var(--oldyellow);
      cursor: pointer;
      border-radius: 100%;
      pointer-events: auto;
    }

    input::-ms-thumb {
      appearance: none;
      height: 1.5em;
      width: 1.5em;
      background: var(--oldyellow);
      cursor: pointer;
      border-radius: 100%;
      pointer-events: auto;
    }
  }

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    &__body {
      display: none;
      visibility: hidden;
      opacity: 0;
    }
    &__price {
      padding-bottom: 20px;
    }
  }
  &__groud {
    position: absolute;
    height: 5px;
    width: 220px;
    border-radius: 5px;
  }

  &__price {
    display: flex;
    height: 100%;
    align-items: flex-start;
    width: 100%;
    margin-top: 8px;
  }
  &__input {
    width: 55px;
    height: 30px;

    input {
      border-radius: 5px;
      width: 100%;
      height: 100%;
      border: 1px solid var(--itemBorder);
      color: var(--itemtext);
      background: var(--poblick);
      padding: 5px;
    }
  }
  &__input:last-child {
    margin-left: 20px;
  }
}
